  font-size: 1.3rem;
  font-weight: 700;
}
.App .anime-list-page .anime_list-header .selector {
  background-color: #3A3A3A;
  border: none;
  width: 95px;
  color: white;
  font-family: "Poppins", sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App .nav {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
